/* Error */
.error {
  font-size: 0.75rem;
  line-height: 1;
  display: none;
  margin-left: 14px;
  margin-top: -0.2865rem;
  margin-bottom: 0.9375rem;
}

.form-group__message,
.error {
  font-size: 0.75rem;
  line-height: 1;
  display: none;
  margin-left: 14px;
  margin-top: -0.2865rem;
  margin-bottom: 0.9375rem;
}

.form-group--error + .error,
.form-group--error .error {
  display: block;
  color: #f57f6c;
}

.has-error input,
.has-error input,
.has-error textarea,
.has-error.select_input,
.has-error input:focus,
.has-error input:hover,
.form-group--error.select_input,
.form-group--error input,
.form-group--error textarea,
.form-group--error input:focus,
.form-group--error input:hover {
  border-color: #f57f6c;
}

