  color: #818181;
}

@-webkit-keyframes shakeError {
  0% {
    transform: translateX(0);
  }

  15% {
    transform: translateX(0.375rem);
  }

  30% {
    transform: translateX(-0.375rem);
  }

  45% {
    transform: translateX(0.375rem);
  }

  60% {
    transform: translateX(-0.375rem);
  }

  75% {
    transform: translateX(0.375rem);
  }

  90% {
    transform: translateX(-0.375rem);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes shakeError {
  0% {
    transform: translateX(0);
  }

  15% {
    transform: translateX(0.375rem);
  }

  30% {
    transform: translateX(-0.375rem);
  }

  45% {
    transform: translateX(0.375rem);
  }

  60% {
    transform: translateX(-0.375rem);
  }

  75% {
    transform: translateX(0.375rem);
  }

  90% {
    transform: translateX(-0.375rem);
  }

  100% {
    transform: translateX(0);
  }
}
/* FORM REALTIME */
#buyValue:not:valid {
  border-color: #2ecc71;
  box-shadow: 0 0 3px#2ecc71;
}

.input-requirements li.valid {
  color: #2ecc71;
}
/* Hide and show related .input-requirements when interacting with input */
input:not([type='submit']) + .input-requirements {
  overflow: hidden;
  max-height: 0;
  transition: max-height 1s ease-out;
}
input:not([type='submit']):hover + .input-requirements,
input:not([type='submit']):focus + .input-requirements,
input:not([type='submit']):active + .input-requirements {
  max-height: 130px; /* any large number (bigger then the .input-requirements list) */
  transition: max-height 1s ease-in;
}
.registration {
  background-color: #fff;
  max-width: 600px;
  margin: 0 auto;
  padding-bottom: 20px;
  box-shadow: 1px 1px 5px 0px rgba(0, 0, 0, 0.3);
  border-bottom: 5px solid #ffdb3a;
}
.input-requirements {
  list-style: disc;
  list-style-position: inside;
  margin: 10px auto;
}
.input-requirements li.invalid {
  color: #e74c3c;
}
.input-requirements li.valid {
  color: #2ecc71;
}
.input-requirements li.valid:after {
  display: inline-block;
  padding-left: 10px;
  content: '\2713';
}
